import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Producto } from 'src/app/Modelo/Producto';
import { ServiceService } from 'src/app/Service/service.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-editar',
  templateUrl: './editar.component.html',
  styleUrls: ['./editar.component.css']
})
export class EditarComponent implements OnInit {
  //public form: FormGroup  
  producto :Producto=new Producto();
  constructor(
   // private formBuilder: FormBuilder   ,
    private router:Router,private service:ServiceService) { 

    /*this.form = this.formBuilder.group({
      codigo: 0,
      Nombre: '',//['', Validators.required],
      Descripcion: '',// ['', Validators.required],
      Marca: ''// ['', Validators.required]
    })*/
  }

  ngOnInit() {
    this.Editar();
  }

  Editar(){
    debugger;
    let codigo=localStorage.getItem("Codigo");
    this.service.getProductoId(+codigo)
    .subscribe(data=>{
      this.producto.idProducto  =data[0].idProducto;
      this.producto.nombre  =data[0].nombre;
      this.producto.descripcion = data[0].descripcion;
      this.producto.imagen = data[0].imagen;
      this.producto.presentacion = data[0].presentacion;
      this.producto.precio = data[0].precio;
      this.producto.contenido = data[0].contenido;
      this.producto.ibu = data[0].ibu;
      this.producto.volumen = data[0].volumen;
      this.producto.stock = data[0].stock;
      this.producto.estado = data[0].estado;
      this.producto.procedencia = data[0].procedencia;
      this.producto.categoria = data[0].categoria;
      this.producto.marca = data[0].marca;
      this.producto.fechaRegistro = data[0].fechaRegistro;
      //this.form.patchValue(data)
    })

  }
  Actualizar(producto:Producto){
    this.service.updateProducto(producto)
    .subscribe(data=>{
      this.producto=data;
      alert("Se Actualizo con Exito...!!!");
      this.router.navigate(["listar"]);
    })
  }
}
